import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import {repeatFloatAdvance} from '../../../actions/loan_actions';
import {Alert, Row, Col, Badge} from 'react-bootstrap';
import '../../../styles/flow.css';
import {Link} from 'react-router-dom';
import {dd_value} from '../../../actions/common_actions';
import DisplayImage from '../../common/component/DisplayImage';
import  {variant_color, format_date} from '../../../helpers/common_helper';
import {person_name, calc_percent} from '../../../helpers/common_helper';
import { Modal , Button} from 'react-bootstrap';
import AccountContainer from '../../account/container/account_container';
import CustomerStatus from '../../common/component/core/CustomerStatus';
import  {lbl_status, style_status, addr_txt,capitalize_first_letter} from '../../../helpers/common_helper';
import {bring_to_probation, closeProfile} from '../../../actions/borrower_actions';
import {submitCheckin} from '../../../actions/field_visit_actions';
import {touch} from '../../../actions/common_actions';
import {ImLocation2} from "react-icons/im";
import {FaAngleDoubleDown} from "react-icons/fa";
import {GiGrowth} from "react-icons/gi";
import {GrView} from "react-icons/gi";
import {get} from '../../../helpers/storage_helper';
import  {check_priv} from '../../../helpers/storage_helper';
import Agreement from './Agreement';
import { event } from 'jquery';



class BorrowerPanel extends FlowComponent {

state = {server_time : {}, 
		checkin_req :{},
		pointer_evt : ''
	}
	componentWillReceiveProps(props){
		if(props.customer.profile_status == 'closed'){
			this.setState({ pointer_evt: 'pointer_evt'});
		}
	}

	handleViewAccountsClick = (event,data_prvdr_cust_id,person_name) => {
    this.setState({cust_id: event.target.id});
    this.data_prvdr_cust_id =  data_prvdr_cust_id;
    this.person_name = person_name;
    this.setState({modalBox1: "true"});   
  }

  	handleModalClose = (event) => {
      this.setState({ modalBox1: false,
      					modalBox2: false });
  } 

  handleCheckIn = (event) =>{
  	
  	const resp = window.confirm("Are you sure to Check-In?")
  	if(resp === false){
			return;
		}else{

  		var checkin_req = this.state.checkin_req
  		checkin_req.cust_id = this.props.customer.cust_id
  		submitCheckin(this.req({checkin_req}))
    	         .then((response) => {
                     if(!response){return };
                 if(response.status === "success"){
                 	const  visit = response.data
                 	var resp = true;
                     if(visit.action == "lastvisit"){
                     	resp = window.confirm(visit.message);
                     }
                     if(resp && visit.visit_id){
                     	window.open(`/checkout/${visit.cust_id}/${visit.visit_id}`,"_blank")
					 }
                     
                 }
                 else{
                   alert(response.message  +"\n" + JSON.stringify(response.data));  
                 }
            })
		}

  	

  	
  	/*
  	

  	
  	this.setState({modalBox2: "true"});
  	const request = {"cust_id" : this.props.customer.cust_id};
		  this.setState({cust_id: this.props.customer.cust_id});

  	touch(this.req(request))
		       .then((response) => {
		       	
		       })*/

  }

  handleSubmit = (event) =>{
  	
  	var checkin_req = this.state.checkin_req
  	checkin_req.cust_id = this.props.customer.cust_id
  	checkin_req.visit_start_time = this.state.server_time.server_time_db
 
  	submitCheckin(this.req({checkin_req}))
		        .then((response) => {
 			      	if(!response){return };
		            if(response.status === "success"){
		            	alert(response.message)
		            	this.setState({modalBox2 : false})
		              
		            }
		            else{
		              alert(response.message + "\n" + JSON.stringify(response.data));  
		            }
		        })
  }

  handleCheckinChange = (event) =>{
  	var checkin_req = this.state.checkin_req
  	checkin_req[event.target.id] = event.target.value
  	 // this.setState({checkin_req[event.target.id] : event.target.value});
  	this.setState({checkin_req});

  }

  	bring_to_probation = (event) => {
		// var cond_msg = this.props.customer.cond_count == 0 
		// ? "This is the customer's first condonation. He can be condonated only 2 times." 
		// : (this.props.customer.cond_count == 1) 
		// 	? "This is the customer's last condonation. If the customer still do not perform well, He can no longer be associated with Flow"
		// 	: false;
		// if(cond_msg == false){
		// 	alert("Condonation count is exhausted for this customer.");
		// 	return;
		// }
		
	  	// const resp = window.confirm("Allowing condonation reset all the past performance of the customer and customer will be able to apply for an FA again only after 7 days.\n\n"+cond_msg+"\n\nAre you sure to continue?");
		// if(resp === false){
		// 	return;
		// }
		  
		  const request = {"cust_id" : this.props.customer.cust_id};
		  
		  bring_to_probation(this.req(request))
		       .then((response) => {
				this.resp(response);
		        if(!response){return };
		            if(response.status === "success"){

		              window.location.reload();
		            }
		            else{
		              alert(response.message + "\n" + JSON.stringify(response.data));  
		            }
		          }
		        ); 
	      //this.setState({credit_score_factor_id: event.target.id, toView: true});
	   } 
	   
	repeatFloatAdvance = (event) => {

		const request = {"cust_id" : this.props.customer.cust_id};
		repeatFloatAdvance(this.req(request))
			.then((response) => {
				if(!response){return };
					if(response){ 

						alert("Float Advance Application created successfully")
						window.location.reload();
					}	
			});
	}

	handleSignAgreement = (event) => {
		var resp = true;
		if(this.props.customer.current_aggr_doc_id){
			resp = window.confirm("An agreement already exists for this customer. Do you want to inactivate it and generate a new agreement? ")
		}
		
		if(resp){
			window.open(`/agreement/upload/${this.props.customer.cust_id}`, "_blank")
		}	
	}

	close_profile = (event) => {
		const resp = window.confirm("Are you sure to close this profile?");
		if(resp === false){
			return;
		}
		const request = {"cust_id" : this.props.customer.cust_id};
		  
		closeProfile(this.req(request))
			.then((response) => {
			this.resp(response);
			if(!response){return };
				if(response.status === "success"){

					window.location.reload();
				}
				else{
					alert(response.message + "\n" + JSON.stringify(response.data));  
				}
				}
			); 
	}
	render(){
		const bring_to_prob_priv = check_priv('customer' , 'bring_to_probation');
		
		return(
			<div class={`${this.state.pointer_evt}`}>
					<div class="container" >
					{this.props.customer.profile_status == 'closed' &&

						<div style={{position:'absolute'}}>
							<div class="closed-stamp">
								<div class="text">Closed</div>
							</div>
						</div>
					}
					<ul className="cust_view_elements px-0  mb-3 ">
						<li>	
							<CustomerStatus show_1l="true" id={this.props.customer.id} entity="borrowers" 
							status={this.props.customer.status} category={this.props.customer.category} 
							prob_fas={this.props.customer.prob_fas} kyc_status={this.props.customer.kyc_status}
							cust_id= {this.props.customer.cust_id} tot_prob_cond_fas = {this.props.customer.tot_prob_cond_fas}
							cond_count = {this.props.customer.cond_count} risk_category={this.props.customer.risk_category}/>&nbsp;

						</li>

						<li class="vl"></li>

						<li>
							<h3 ><Badge className="badge" pill variant={variant_color(this.props.customer.kyc_status)}>
								<span>KYC : {capitalize_first_letter(this.props.customer.kyc_status)}&nbsp;</span>
							</Badge></h3>
						</li>
						<li>
							{check_priv('customer','perform_kyc') &&
								<Link to={"/kyc/"+this.props.customer.cust_id} >Perform KYC</Link>
							}
						</li>
					</ul>
					
					
					
					<ul className="cust_view_elements px-0 mb-0">
						{/*get('user_id') == this.props.customer.flow_rel_mgr_id*/}
						{check_priv('field_visit','checkin') && 
							<li  >
								<Button variant = "success" onClick={this.handleCheckIn}><ImLocation2 className ="mr-1 " color="white"/>Checkin</Button>
								
							</li>
						}
						
						<li className = "ml-5" >
							<input id={this.props.customer.cust_id} type='button' 
							onClick={(e) => this.handleViewAccountsClick(e, this.props.customer.data_prvdr_cust_id,person_name(this.props.customer))} 
							value='View A/C'  className={`btn btn-primary ${this.btn_style}`} />&nbsp;
						</li>


						
						{/* {(this.props.customer.prob_fas == 0 || this.props.customer.prob_fas == null)&& bring_to_prob_priv &&
							this.props.customer.all_ineligible &&  */}
						<li><Button variant="danger" disabled={this.state.calling_api} onClick={this.bring_to_probation}>Allow Condonation</Button> 
						<span style={{fontSize:'18px'}}><b> ({this.props.customer.cond_count}/{this.props.customer.max_allowed_condonation})</b></span></li>
						
						{/* { this.props.customer.ongoing_loan_doc_id == null && this.props.customer.pending_loan_appl_doc_id == null &&  
							<td><Button onClick={this.repeatFloatAdvance}>Repeat FA</Button></td>} */}
						{check_priv('customer','close') && 
							<li><Button variant="danger" disabled={this.state.calling_api} onClick={this.close_profile}>Close Profile</Button></li>
						}
						<li><h3 ><Badge className="badge" pill variant='warning'>
								<span>Activity Status : {capitalize_first_letter(this.props.customer.activity_status)}&nbsp;</span>
							</Badge></h3></li>
						{check_priv('customer' , 'risk_category') &&
							<li><h3 ><Badge className="badge" pill variant='secondary'>
								<span>Risk Category : {dd_value(this.props.customer.risk_category)}&nbsp;</span>
							</Badge></h3></li>
                        }

					</ul>
					<ul className="cust_view_elements"  >
					{check_priv('field_visit','checkin') && 
						this.props.customer.last_visit_date && <h6 ><span>Last Visit</span>&nbsp; <b>{format_date(this.props.customer.last_visit_date)}</b></h6>}
				
					</ul>

					<ul className="cust_view_elements px-0 ">
						{this.props.customer.ongoing_loan_doc_id && 
						<>
							<li >
								<Link to={"/fa/view/"+this.props.customer.ongoing_loan_doc_id} target="_blank">View Ongoing FA</Link>
							</li>
						
							< li className = "ml-5">|</li>
						</>
						}

						<li>
							<Link to={"/fa/view/"+this.props.customer.last_loan_doc_id} target="_blank">View Last FA</Link>
						</li>
						
						< li className = "ml-5">|</li>


						<li>
							<Link to={"/field_visit/search/"+this.props.customer.cust_id} target="_blank">Recent Visits</Link>
						</li>

						< li className = "ml-5">|</li>


						<li>
							<Link to={"/call_log/search/"+this.props.customer.cust_id} target="_blank">Recent Calls</Link>
						</li>
						
						< li className = "ml-5">|</li>
						
						<li>
							<Link to={"/report/growth_chart_for/"+this.props.customer.cust_id} target="_blank" >
								<GiGrowth className ="mr-1" color="white"/>Growth</Link>
						</li>
						
						
						
					</ul>	
					</div>
					
					<div className="panel_border mt-1">					
						<Col lg="12">
							<Row>
								<Col>Data Provider Customer ID</Col>
								<Col><b>{this.props.customer.data_prvdr_cust_id}</b></Col>
								<Col>FLOW Customer ID</Col>
								<Col><b>{this.props.customer.cust_id}</b></Col>
							</Row>

							<Row>
								<Col>Business Name</Col>
								<Col><b>{this.props.customer.biz_name}</b></Col>
								<Col>Data Provider</Col>
								<Col><b>{this.props.customer.data_prvdr_name}</b></Col>
							</Row>
							

							<Row>
								<Col>Establishment Address Type</Col>
								<Col><b>{dd_value(this.props.customer.biz_addr_prop_type)}</b></Col>
								<Col>Lender</Col>
								<Col><b>{this.props.customer.lender_name}</b></Col>
							</Row>
							<Row>
								<Col>Ownership</Col>
								<Col><b>{this.props.customer.ownership}</b></Col>
								<Col>Data Provider's Relationship Manager</Col>
								<Col><b>{this.props.customer.dp_rel_mgr_name}</b></Col>
							</Row>
							<Row>
								<Col>Business Distance from Home</Col>
								<Col><b>{dd_value(this.props.customer.business_distance)}</b></Col>
								<Col></Col>
								<Col></Col>
							</Row>
							<Row>
								
								<Col>Flow Relationship Manager</Col>
								<Col><b>{this.props.customer.flow_rel_mgr_name}</b></Col>
								<Col>Score Run ID</Col>
								<Col><b>{this.props.customer.csf_run_id}</b></Col>
							</Row>
							<Row>
								<Col>Registered Date</Col>
								<Col><b>{format_date(this.props.customer.reg_date)}</b></Col>
								
								
								<Col>
								{this.props.customer.photo_biz_lic ?
								<Link to={this.props.customer.photo_biz_lic_path + "/l_" + this.props.customer.photo_biz_lic} target="_blank">
								Business Licence Photo
								</Link> 
								: 
								<p>Business Licence Photo : Not uploaded</p>
								}
								</Col>
								<Col></Col>
							</Row>

							{/*<Row>
								<Col><Link to={this.props.customer.photo_biz_lic_path + "/l_" + this.props.customer.photo_biz_lic} target="_blank">
								Business Licence Photo
								</Link></Col>
							</Row>*/}
							<Row>
								<Col>
									<DisplayImage title="Selfie" image_path={this.props.customer.photo_selfie_path} file_name={this.props.customer.photo_selfie} />
								</Col>
								<Col>
									
									<DisplayImage title="Owner Photo" image_path={this.props.customer.photo_pps_path} file_name={this.props.customer.photo_pps} />
									{/*<b>Owner Photo</b><br/>
									<img src={this.root + this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps} 
									alt={this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps}/>*/}
								</Col>
								<Col>
									
									<DisplayImage title="Establishment Photo" image_path={this.props.customer.photo_shop_path} file_name={this.props.customer.photo_shop} />
									{/*<b>Establishment Photo</b><br/>
									<img src={this.root + this.props.customer.photo_shop_path + "/s_" + this.props.customer.photo_shop} 
							alt={this.props.customer.photo_shop_path + "/s_" + this.props.customer.photo_shop}
							/>*/}
								</Col>
							</Row>

						</Col>
						{/*<Col lg="3">
							<b>Owner Photo</b><br/>

							<img src={this.root + this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps} 
							alt={this.props.customer.photo_pps_path + "/s_" + this.props.customer.photo_pps}/>
						</Col>*/}


					</div>
					<Row style={{padding:'20px 0 0 0'}}>
						      <Col md={2}>
		                       <Alert.Heading><b>Agreement</b></Alert.Heading>
						      </Col>
					</Row>
					<div className="panel_border">
						{this.props.customer.aggr_status !='inactive' &&
							<Agreement mode="create" variant={this.state.variant} 
							customer={this.props.customer} 
							/>
						}	
						{this.props.customer.account_id ?
							<div style={{width:'100%'}}>
								<Link to={this.props.customer.cust_id ? `/agreement/list/${this.props.customer.cust_id}` : "#"} 	 
								className="agreement" 
								onClick={this.handleAgreementLinkClick} target="_blank">List Customer Agreements</Link>
								{/*check_priv('cust_agreement','create') &&
									<Link to={this.props.customer.cust_id ? `/agreement/create/${this.props.customer.cust_id}` : "#"}
									className= "agreement" 
									onClick={this.handleAgreementLinkClick} target="_blank">Generate New Customer Agreement</Link>
								*/}
								{check_priv('cust_agreement','sign_upload') &&
									<Link className="agreement" to = '#'
									onClick={this.handleSignAgreement}>Sign Agreement</Link>
								}
							</div>	
							:
							<div>Please add a primary account to create the agreement.</div>	
						}
					</div>
							
					<Modal show={this.state.modalBox1} onHide={this.handleModalClose} size="xl">
						<Modal.Header closeButton show={this.state.modalBox1} onHide={this.handleModalClose}>
							<Modal.Title>Customer Accounts : {this.state.cust_id}</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<AccountContainer entity="customer" dp_cust_id={this.data_prvdr_cust_id} cust_name={this.person_name} entity="customer" entity_id={this.state.cust_id}/>
						</Modal.Body>
						<Modal.Footer>
							<Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
						</Modal.Footer>
					</Modal>


					<Modal show={this.state.modalBox2} onHide={this.handleModalClose} size="l">
						<Modal.Header closeButton show={this.state.modalBox1} onHide={this.handleModalClose}>
							<Modal.Title>Check In</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<div className="container">
							 	<form>
				                	<div className="rule-form-group  form-group row reset-margin">
				                  		<label for="" className="col-sm-5 mb-3 label__name font-weight-bold reset-padding">Flow Cust ID | DP Cust ID</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		<label className="" id = "cust_id" >{this.props.customer.cust_id} | {this.props.customer.data_prvdr_cust_id}</label>
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				          
				                  	</div>
				                  
				                  	<div className="rule-form-group form-group row reset-margin">
				                  		<label for="" className="col-sm-5 mb-3 font-weight-bold  label__name reset-padding">Address</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		<label className="" id = "cust_id">{this.props.customer.cust_addr_txt}</label>
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				          
				                  	</div>
				                  	<div className="rule-form-group form-group row reset-margin">
				                  		<label for="" className="col-sm-5 mb-3 font-weight-bold  label__name reset-padding">Check In At</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
					                    		<label className="" id = "visit_start_time">{this.state.server_time.server_time_ui}</label>
						                  		
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				                     
				                  	</div>
				                  	<div className="rule-form-group form-group row reset-margin">
				                  		<label for="" className="col-sm-5 font-weight-bold label__name reset-padding">Time Spent</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		<select id = "time_spent"  className = "form-control mb-3" onChange={(e) =>{this.handleCheckinChange(e);}}>
						                  			<option value ="" >choose</option>
						                  			<option value ="5">5 Minutes</option>
						                  			<option value ="10">10 Minutes</option>
						                  			<option value ="15">15 Minutes</option>
						                  			<option value ="20">20 Minutes</option>
						                  			<option value ="25">25 Minutes</option>
						                  			<option value ="30">30 Minutes</option>
						                  		</select>
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				                     
				                  	</div>
				                  	<div className="rule-form-group form-group row reset-margin ">
				                  		<label for=""  className="col-sm-5 font-weight-bold label__name reset-padding">Visit Purpose</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		<select  id="visit_purpose" className = "form-control mb-3" onChange={(e) =>{this.handleCheckinChange(e);}} >
                									{this.getSelectOptions("visit_purpose")}
               									</select>
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				                     
				                  	</div>
				                  	<div className="rule-form-group form-group row reset-margin">
				                  		<label for="" className="col-sm-5 font-weight-bold label__name reset-padding">Remarks</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		<input autocomplete="off"  type="text" id="remarks" className="form-control" onChange={(e) =>{this.handleCheckinChange(e);}}  maxLength='50'/>
						                  		
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				                     
				                  	</div>



				                  	<div className="rule-form-group form-group row reset-margin">
				                  	<label class="cameraButton">Take a picture</label>
					                    <div className="col-sm-7 reset-padding">
					                    	<div>
						                  		 	 
    											<input type="file" accept="image/*" capture=""/>
  									
						                  		
                        					</div>
					                    	<div className="control-me"></div>
					                    </div>
				                     
				                  	</div>

				                </form>
							</div>
						</Modal.Body>
						<Modal.Footer>
							<button type="button" className= "btn btn-primary float-right mr-1" onClick={this.handleSubmit} disable>Submit</button>
							<Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
						</Modal.Footer>
					</Modal>
			</div>
			);
	}

}

export default BorrowerPanel;
