import {  Redirect } from 'react-router-dom';
import React from 'react';
import FlowContainer from "../../common/container/core/FlowContainer";
import FlowComponent from "../../common/component/core/FlowComponent";
import Person from "../../common/component/Person";
import '../../../styles/flow.css';
import {createPerson, viewPerson} from "../../../actions/person_actions";
import {get,get_item} from "../../../helpers/storage_helper";
import {title_case} from "../../../helpers/common_helper";

class CreateUserContainer extends FlowContainer {

  state = {
    person: {},
    address : {},

  }

  componentWillMount(){
      var person = null;
      if(this.props.mode === 'view'){
          const person_id = this.props.match.params.person_id;
          const request = {person_id};
          viewPerson(this.req(request))
            .then((response) => {
              if(!response){return };
                person = response.data;
                this.setState({person: person,
                                address : person.address
                });
              }
            );
        }

  }

  handleUserCheckboxClick = (event) => {
  if(event.target.checked){
    this.setState({create_user: true});
  }else{
    this.setState({create_user: false});
  }
}

  handleSubmit = (event) => {
    event.preventDefault();
    var person = this.state.person;
    person.country_code = this.country_code;
    person.address = this.state.address;
    person.associated_with = 'FLOW';

    createPerson(this.req({person}))
        .then((response) => {
            this.resp(response)
          if(!response){return };
              alert("New Person created successfully");
              window.location.reload()
          }
        );
  }

  handleRoleSelect = (event) => {
      this.state.person.role = event.target.value
  }


  render(){

      return (

      <div className="container">
         <h4>{this.props.title + (this.state.person.first_name? " : " + this.state.person.first_name : "")}</h4>
        <form id="borrower_form" onSubmit={this.handleSubmit}>
            <div id="">

             <Person id="person" title="" entity="user" designation='list' data={this.state.person}
                    mode={this.props.mode}
                    onComponentChange={this.handleComponentChange} changeComponentName="person" />
              <br/>

            <input type="button" className={`btn btn-primary ${this.props.mode === "create" ? "show_button": "hide_button"}`} onClick={this.handleSubmit} value="Create Person" disabled={this.state.calling_api}/>
          </div>

        </form>
        <br/><br/>
      </div>

      );
  }
}

export default CreateUserContainer;
