import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {borrowerSearch} from '../../actions/borrower_actions';
import ReactTable from "react-table"; 
import {format_date_tbl,capitalize_first_letter} from '../../helpers/common_helper';
import { Link} from 'react-router-dom';






class KycPendingList extends FlowComponent {

	state = {
		borrower_data : {},
		columns :[],
		list : []
	}

	componentWillMount() {
		var borrower_search_criteria = this.state.borrower_data;
		this.state.borrower_data.pending_kyc = "true"
      	borrowerSearch(this.req({borrower_search_criteria}))
		        .then((response) => {
		        	if(!response){return };
		        	if(response.status === "success"){
		        		var columns = this.state.columns
		        		columns = [{
		        			Header : "Cust ID",
		        			accessor: 'cust_id' ,
		        			Cell: e =>  <Link to={"/borrower/indiv/view/"+e.value} target="_blank" >{e.value}</Link>,

		        		},
		        		{
		        			Header : "Biz Name",
		        			id: 'biz_name', 
		        			accessor: 'biz_name',
		        		},
						{
		        			Header : "Reg Date",
		        			id: 'date', 
		        			accessor:row => format_date_tbl(row.reg_date),
		        		},
		        		{
		        			Header : "Data Prvdr Code",
		        			accessor: 'data_prvdr_code' ,
		        		},
		        		{
		        			Header : "Data Prvdr Cust ID",
		        			accessor: 'data_prvdr_cust_id' ,
		        		},
		        		{
		        			Header : "RM Name",
		        			accessor: 'flow_rel_mgr_name' ,
		        		},
		        		
		        		{
		        			Header : "Action",		        			
		        			Cell: row =>  <Link to={"/kyc/"+ row.original.cust_id} target="_blank" >Perform KYC</Link>,

		        		}

		        		]

		        		this.setState({columns,lists:response.data.results})
		        	}



		        })
		
    }



    render(){
        return (

        	<div>
				<h3 class="text-center my-4"><b>Customers with Pending KYC</b></h3>
				<ReactTable  minRows = {2}  data={this.state.lists}  columns={this.state.columns}  defaultSorted={[{id: "date",
              desc: false}]}  />
        	</div>
        	)

    }
}


export default KycPendingList;