import {call_fetch, admin_path, app_path} from './../helpers/fetch_helper';


export const getFieldVisitData = request  => {
   return call_fetch(admin_path + '/field_visits/search', request);
}

export const submitCheckin = request  => {
   return call_fetch(admin_path + '/field_visits/checkin', request);
}

export const submitCheckout = request  => {
   return call_fetch(admin_path + '/field_visits/checkout', request);
}

export const checkLastVisit = request  => {
   return call_fetch(admin_path + '/field_visits/validate', request);
}

export const fieldVisits = request  => {
   return call_fetch(admin_path + '/field_visits/details', request);
}

