import {call_fetch, admin_path, app_path} from '../helpers/fetch_helper';

export const createPerson = request => {
   return call_fetch(admin_path + '/person/create', request);
}

export const viewPerson = request => {
   return call_fetch(admin_path + '/person/view', request)
}

export const listPerson = request => {
   return call_fetch(admin_path + '/person/list', request)
}

export const resetAppUserPassword = request => {
   return call_fetch(admin_path + '/user/app/password/email', request)
}

export const resetInvUserPassword = request => {
   return call_fetch(admin_path + '/user/inv/password/email', request)
}