import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {Link} from 'react-router-dom';
import { get } from '../../helpers/storage_helper';
import {borrowerSearch} from '../../actions/borrower_actions';
import {dd} from '../../actions/common_actions';
import  {check_priv} from '../../helpers/storage_helper';
import  {format_month_change} from '../../helpers/common_helper';
import '../../styles/flow.css';
import $ from 'jquery';

class ExpiredAgreement extends FlowComponent {
    
    componentWillMount(){
        const borrower_search_criteria = {have_expired_agrmnt : "true",
        belong_to_user : "true"};

        borrowerSearch(this.req({borrower_search_criteria}, true))
        .then((response) => {
            if(!response){return };
            if(response){
                var borrowerSearchResults =[];
                borrowerSearchResults = response.data.results.map((borrower,i)=> <tr>
                      <td>{borrower.data_prvdr_cust_id}</td>
                      <td>{borrower.first_name} {borrower.middle_name} {borrower.last_name}</td>
                      <td><Link to={"/borrower/indiv/view/"+borrower.cust_id} target="_blank">{borrower.cust_id}</Link></td>
                      <td>{borrower.biz_name}</td>
                      <td>{borrower.category}</td>
                      <td>{borrower.aggr_valid_upto != null ? format_month_change(borrower.aggr_valid_upto) : "-NA-"}</td>
                      <td>{borrower.prob_fas ==1 ?"Completing "+borrower.category : "Agreement Expired"}</td>
                      <td>{check_priv('cust_agreement','sign_upload') &&
						   <Link to={"/agreement/upload/"+borrower.cust_id} target="_blank">Renew</Link>}</td>
                  </tr>
                  );

                  this.setState({ borrowerSearchResults:  borrowerSearchResults});
                        $('#borrowers_list').removeAttr('width').dataTable({
                            "order" : [],
                            "columns": [
                                {"width": "5%"},
                                {"width": "5%"}, 
                                {"width": "5%"}, 
                                {"width": "5%"}, 
                                {"width": "5%"}, 
                                {"width": "5%"}, 
                                {"width": "5%"},                                      
                                {"width": "5%"},                                      
                             ],
                            "pageLength" : 5,
                         });
            }
        });
    }

    render(){
        return(
            <div>
                <div className="home-text"><h4><b>Agreements Renewal</b></h4></div>
                <table id ='borrowers_list' className='display strip row-border order-column list'>
                  <thead>
                  <tr><th>Data Provider Cust ID</th>     
	                  <th>Cust Name</th>
	                  <th>Cust ID</th>
	                  <th>Biz Name</th>
	                  <th>Category</th>
	                  <th>Agreement Valid Upto</th>
                      <th>Renewal Reason</th>
                      <th>Action</th>
                  </tr>
                  </thead>
                  <tbody> 
                 {this.state.borrowerSearchResults}
                  </tbody>
                </table>   
            </div>	
        );
      }
}
export default ExpiredAgreement;
