import React from 'react';
import ReactTable from "react-table";
import FlowComponent from '../../common/component/core/FlowComponent';
import {searchLoan} from '../../../actions/loan_actions';
import {format_date_tbl, capitalize_first_letter, title_case} from '../../../helpers/common_helper';
import {TooltipLabel} from '../../common/partial';
import {OverlayTrigger,Tooltip} from 'react-bootstrap';
import {dd_value} from '../../../actions/common_actions';
import {lbl_amt} from '../../../helpers/common_helper';

class LastNFAs extends FlowComponent {

    state = {
        columns :[],
        loans : [],
        data :{}
    }
    componentWillMount(){
        const loan_search_criteria = {'cust_id' : this.props.cust_id, 'last_n_fas' : this.props.last_n_fas, 'mode' : 'search'};
        console.log(loan_search_criteria);
        if(this.props.cust_id){
            searchLoan(this.req({loan_search_criteria}, true))
            .then((response) => {	
            
            if(!response){return };
            
            if(response.status === "success" && response.data.results.length > 0){
                //this.setState({data : response.data.results});
                var columns = []
					columns = [
                        {  
                        Header: ()=> <b>Disbursal Date</b>,  
                        id:"due_date",
                        accessor:row => format_date_tbl(row.disbursal_date,true),
                        //accessor:'disbursal_date',
                        
                        },
                        {  
                        Header: ()=> <b>Paid Date</b>,  
                        id:"paid_date",
                        accessor:row => format_date_tbl(row.paid_date,true)
                        },
                        {  
                        Header:()=> <b>FA Amount</b>,
                        id:"loan_principal",
                        accessor:row => lbl_amt(row.loan_principal,this.currency_code)
                        },
                        {  
                        Header: ()=> <b>Overdue Days</b>,  
                        accessor:'overdue_days',
                        
                        },
                        {  
                        Header: ()=> <b>Approver Name</b>,  
                        accessor: 'loan_approver_name',
                        //Cell: e =>  <TooltipLabel full_text={e.value} overlay={e.original.approver_role}/>
                        Cell : e => <OverlayTrigger overlay={<Tooltip id="tooltip">{e.original.approver_role}</Tooltip>}>
                                    <span>{e.value}</span>
                                    </OverlayTrigger>
                        },
                        {  
                        Header: ()=> <b>Eligibility</b>, 
                        id:"result_code", 
                        accessor:row => capitalize_first_letter(row.cs_result_code,true)
                        
                        //accessor:row => title_case(row.cs_result_code)
                        },
                        {  
                        Header: ()=> <b>Status</b>,  
                        id:"status",
                        accessor:row => dd_value(row.status),
                        //accessor:'disbursal_date',
                        
                        }

                    ]
					this.setState({loans : response.data.results,columns, length : response.data.results.length});
                }
            });
        }
        
    }
    render(){
        
        return (
            <div>
                <h4>Last {this.props.last_n_fas} FAs</h4>
                <ReactTable  minRows = {3}  data={this.state.loans}  columns={this.state.columns} showPagination ={false} />
            </div>
            )
    }
}

export default LastNFAs;