import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {changeDisbursalStatus, getDisburseAttempt, retryDisbursal, searchHeldLoan} from '../../actions/loan_actions';
import {format_date_tbl, format_date, title_case, lbl_amt, get_time_diff} from '../../helpers/common_helper';
import CaptureFailedDisbursal from '../loan/component/CaptureFailedDisbursal';
import '../../styles/flow.css';
import $ from 'jquery';
import {Link} from "react-router-dom";
import {dd_value} from "../../actions/common_actions";
import { check_priv } from '../../helpers/storage_helper';
import {OverlayTrigger, Popover} from "react-bootstrap";
import {FcInfo} from 'react-icons/fc'
import LoanDates from "../common/partials/LoanDates";
import {TooltipLabel} from "../common/partial";
import {GrRefresh} from "@react-icons/all-files/gr/GrRefresh";

class OnHoldList extends FlowComponent {


    componentWillMount() {
        this.drawTable()
        // setInterval(() => this.refreshList(),60000)
    }

    drawTable(){
        this.server_time(this.time_zone)
        const loan_search_criteria = {status : "stalled", disburse_attempt: true, mode : 'search'};
        searchHeldLoan(this.req({loan_search_criteria}, true))
        .then((response) => {
            function getOverlayInfo (disb_status, loan){
                let msg
                if (disb_status === "failed"){
                    msg = "Float not transferred. App Support Will Retry"
                }
                else if (disb_status === "capture_failed"){
                    msg = "Float Transferred. Not Captured on Flow App. Customer Success will Capture"
                }
                else if (disb_status === "unknown"){
                    msg = "App Support Will Process"
                }
                else if (disb_status === "sent_to_disbursal_queue"){
                    msg = "Float will be transferred shortly"
                }
                else if (disb_status === null && !loan.customer_consent_rcvd){
                    msg = "Customer has not sent the confirmation code"
                }
                return msg
            }
            this.resp(response)
            if(!response){return};
            if(response){
                var loanResults =[];
                loanResults = response.data.results.map((loan)=> <tr>

                    <td>{loan.acc_prvdr_code}</td>
                    <td><Link to={"/fa/view/"+loan.loan_doc_id} target="_blank">{loan.loan_doc_id}</Link></td>
                    <td><Link to={"/borrower/indiv/view/"+loan.cust_id} target="_blank">{loan.data_prvdr_cust_id}</Link></td>
                    <td><TooltipLabel full_text={loan.biz_name} overlay={loan.cust_name} no_reduce={true} /></td>
                    <td>{lbl_amt(loan.loan_principal,this.currency_code)}</td>
                    <td>{loan.attempts}</td>
                    <td>{<OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}> <LoanDates data={loan}/> </Popover>}><span>{get_time_diff(this.state.server_time.server_time_db, loan.attempt_time)} <sup><FcInfo size={14}/></sup></span></OverlayTrigger>}</td>
                    <td>{title_case(loan.status)}</td>
                    <td><OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}>{getOverlayInfo(loan.disbursal_status, loan)}</Popover>}><span>{title_case(loan.disbursal_status || "not_initiated")} <sup><FcInfo size={14}/></sup></span></OverlayTrigger></td>
                    <td>
                        {this.buttonName(loan)}
                    </td>
                    
                    </tr>
                    ); 
           
                    this.setState({loanResults: loanResults, refresh: false});

                        $('#loan_list-b').removeAttr('width').dataTable({
                            "order" : [[8,'desc'],[6,'desc']],
                            "columns": [
                                {"width": "5%"}, 
                                {"width": "5%"}, 
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                                {"width": "5%"},
                             ],
                            "searching": false,
                            "paging": false,
                            "info": false,
                    });
                }
        });
    }


    statusChange (event,loan) {


        let value = event.target.value
        let alert_msg = ""
        if (value === 'capture_failed') {
            alert_msg = "Are you sure if the disbursal attempt is successful and there is a debit transaction to the customer found in the Data Provider A/C statement on " + loan.attempt_time+"?"
        } else if (value === 'failed') {
            alert_msg = "Are you sure the disbursal attempt got failed and there is no debit transaction to the customer found in the Data Provider A/C statement?"
        }
        if (window.confirm(alert_msg)) {
            const request = {loan_doc_id: loan.loan_doc_id, disbursal_status: value}
            changeDisbursalStatus(this.req(request)).then((res) => {
                this.resp(res)
                if (res && res.status == 'success') {
                    this.refreshList()
                }
            })

        }
    }

    refreshList = () => {
            this.setState({refresh: true})
            $('#loan_list-b').DataTable().destroy();
            this.drawTable()

    }


    buttonName = (loan) => {
        let value
        if(loan.disbursal_status == 'capture_failed' && check_priv('loan','home_capture_disbursal')){

           value =  <CaptureFailedDisbursal id="disbursal_txn" data={loan} title="Disbursal Loan" />
        }
        else if(loan.disbursal_status == 'failed' && check_priv('loan','retry_disbursal')){
           value = <input style={{marginBottom: '1%'}} id={loan.loan_doc_id} type='submit' value ='Retry' disabled={this.state.calling_api} onClick={(event) => {this.retryFA(loan.loan_doc_id)}} className='btn btn-primary'/>
        }
        else if(loan.disbursal_status == 'unknown' && check_priv('loan','release')){
            value = <select style={{minHeight:'30px'}} onChange={(event) => {this.statusChange(event,loan)}}>
                    <option disabled selected hidden>choose</option>
                    <option value="capture_failed">Capture Failed</option>
                    <option value="failed">Failed</option></select>
        }

        return  value
    }

    retryFA = (loan_doc_id) => {
        if(this.state.calling_api){return}
        if(!window.confirm("Have you verified in the Data Provider's A/C statement if there is no debit transaction found related to this FA?\n\nPlease note that clicking OK will re-attempt Disbursal")){return}
        retryDisbursal(this.req({'loan_doc_id' : loan_doc_id,}, true)).then((response) => {
            this.resp(response)
            window.location.reload()
        })

    
	}

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.refresh != this.props.refresh){
            this.refreshList()
        }
    }


    render(){
        return (
        <div>
                <div className="home-text"><h4><b>FAs Pending Disbursal</b> {this.state.refresh && <GrRefresh/>}</h4></div>
                <table id ='loan_list-b' className='display strip row-border order-column list'>
                  <thead>
                  <tr><th>ACC PRVDR</th>
                      <th>FA ID</th>
	                  <th>Data Provider Cust ID</th>
	                  <th>Biz Name</th>
                      <th>FA Amount</th>
                      <th>Total Attempts</th>
                      <th>Attempted</th>
                      <th>FA Status</th>
	                  <th>Disbursal Status</th>
                      <th>Action</th>
	                 
                  </tr>
                  </thead>
                  <tbody> 
                 {this.state.loanResults}
                  </tbody>
                </table>   
            </div>	
        );
    
}


}
export default OnHoldList;
