import React from 'react';
import { Redirect } from 'react-router-dom';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import $ from 'jquery';
import {listPerson} from "../../../actions/person_actions";
import FlowContainer from "../../common/container/core/FlowContainer";
import StatusButton from "../../common/component/core/StatusButton";
import ViewEdit from "../../common/component/ViewEdit";
import RelationshipManager from "../../relationship_manager/component/RelationshipManager";
import {person_name} from "../../../helpers/common_helper";

class ListUserContainer extends FlowContainer {

  state = {
     userList: null,
     toView: false,
     person_id: null ,
     data_prvdr: {}

  }

  handleClick = (event) => {
        this.setState({person_id: event.target.id, toView: true});
    }
    componentWillMount(){


    listPerson(this.req({'associated_with':'FLOW'}))
        .then((response) => {
          if(!response){return };
              const userList = response.data.map((person,i)=>
              <tr key={i}>

                <td > {person_name(person)}</td>
                <td > {person.whatsapp}</td>
                <td > {person.mobile_num}</td>
                <td > {person.email_id}</td>
                <td > {person.designation}</td>
                <StatusButton id={person.id} entity="users" status={person.status}/>
                <td><ViewEdit id={person.id} entity="user"  onClick={this.handleClick}/></td>
              </tr>
                                              );
          this.setState({userList: userList});
                 $('#user-list').removeAttr('width').dataTable({
                    "order" : [],
                  "columns": [
                       {"width" : "15%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"}
                     ],
                  dom:'Bfrtip',
                  buttons: [
                  'csv','excel','print'
                  ]
              });
        });
  }

  render(){

     if (this.state.toView === true) {
           return <Redirect to={`/user/view/${this.state.person_id}`} />
    }

    return(

    <div className="container">

       {(this.state.userList != null) &&

          <div id='datatable data_prvdr_table'>
            <h4>Person List</h4>
                 <table id ='user-list'  className="display strip row-border order-column list">
                          <thead>
                          <tr>
                          <th>Name</th>
                          <th>WhatsApp Number</th>
                          <th>Mobile Number</th>
                          <th>Email ID</th>
                          <th>Designation</th>
                          <th>Status</th>
                          <th>Action</th>
                          <th>Action</th></tr></thead>
                          <tbody>
                          {this.state.userList}
                          </tbody>
                 </table>

            </div>
       }

        </div>
    );
}
}
export default ListUserContainer;
