import {call_fetch, admin_path, app_path} from './../helpers/fetch_helper';

export const StartCallLog = request  => {
   return call_fetch(admin_path + '/call_log/start', request);
}
export const CompleteCall = request  => {
   return call_fetch(admin_path + '/call_log/complete', request);
}
export const CancelCall = request  => {
   return call_fetch(admin_path + '/call_log/cancel', request);
}

export const getCallLogs = request  => {
   return call_fetch(admin_path + '/call_log/search', request);
}

export const callLogs = request => {
   return call_fetch(admin_path + '/call_log/details', request);

}